const Videocomp=(props)=>{
    const [check,setcheck]=useState(props.checked);
    console.log(check);
    const [tools,settools] = useState(false);
    return(
        <Box width="xxlarge" direction="row"  align="center" justify='between' margin={{top:"30px"}} >
            <Box direction="row" gap="small">
                <CheckBox checked={check} onChange={(event) => setcheck(event.target.checked)}  />
                <Box direction="row-responsive" justify="start" align="center" gap="small">
                            
                            <Box className="vid" align="center" background="dark-1" justify="center" >Video</Box>
                            <Text className="stl" weight="bold" size="small" >{props.tittle}</Text>

                        </Box>
            </Box>
                    <Box onMouseEnter={()=>{settools(true);}} className="hd" onMouseLeave={()=>{settools(false);}}  direction="row" justify="center" align="center">
                        {tools && <Box animation={{type:"slideLeft",duration:"3000"}} round="small" elevation="medium" fill gap="small"  direction="row" justify="center" background="dark-2" align="center">
                            <Play />
                            <Pause />
                            <FastForward/>
                            <Rewind/>
                            </Box>}

                    </Box>
                    <Box  gap="medium" direction="row" justify="start" align="center" >
                    <Box className="adj" justify="center" align="center">
                        <Checkmark />

                        </Box>
                        <Box className="adj" justify="center" align="center">
                            <Text className="stl" weight="bold" size="small" >{props.addedon}</Text>

                        </Box>
                        
                        <Box className="adj"  justify="end" align="center">
                            <Checkmark />
                        </Box>
                        
                        <Box className="adj"  alignContent="end" justify="end" align="center">
                            <Checkmark />
                        </Box>
                        

                    </Box>

            </Box>

    )
}
function Vdeomngr(props) {
    const [val,setval]=useState("all");
    const[tick,settick]=useState(false);
    const[search,setsearch] =useState("");
    const [searchResults, setSearchResults] = useState(detail);
    useEffect(() => {
        const results = detail.filter((inform)=>inform.tittle.includes(search));
        console.log(results);
        setSearchResults(results);
      }, [search]);
    const [contentperpage,setcontentperpage]= useState(5);
    const [currentpage,setcurrentpage]= useState(1);
    const[videoperpage,setvideoperpage]=useState(5);
    
    
    const lastindexofvideo= currentpage*videoperpage;
    const firstindexofvideo = lastindexofvideo - videoperpage;
    const paginate=(num)=> setcurrentpage(num);
    
    return (
        <>
        <Box fill direction="column" background="white" elevation="medium" gap="medium" height="auto" round="small" pad="small">
            <Box width="xxlarge" direction="row" justify="between">
                <Box direction="row" gap="small" >
                    <Text className="stl" size="medium">Videos</Text>
                    <Box background="neutral-4" width="30px" direction="row" justify="center" round="full">{detail.length}</Box>
                </Box>
                <Box background="neutral-4" width="30px" direction="row" justify="center" round="full">?</Box>
            </Box>
            <Box width="xxlarge" direction="row" justify="end" gap="small">
                <Box width="small" direction="row" justify="center" align="center" elevation="medium">
                    <TextInput plain placeholder="Search by name" value={search} onChange={(event)=> {setsearch(event.target.value);}}></TextInput>
                    <Search  />

                </Box>
                <Box width="auto"  direction="row" justify="center" align="center" elevation="medium">
                    
                    <Select plain placeholder={<Text weight="bold">Filetr by Category:</Text>}
                        options={['small', 'medium', 'large']}
                        value={val}
                        onChange={({ option }) => setval(option)}
                    />

                </Box>
                
            </Box>
            <Box width="xxlarge" direction="row" gap="medium" justify="between" >
                    <Box width={{min:"auto",max:"150px"}} direction="row" justify="start" align="center" gap="small">
                        <CheckBox checked={tick} onChange={(event) => settick(event.target.checked)}   />
                        <Text className="stl" size="small"  weight="bold" >Videos</Text>

                    </Box>
                    <Box  gap="small" direction="row" justify="end" align="center" >
                    <Box className="adj">
                    <Text className="stl" weight="bold" size="small" >Type</Text>

                        </Box>
                        <Box className="adj">
                        <Text className="stl" weight="bold" size="small" >Added on</Text>

                        </Box>
                        <Box className="adj">
                        <Text className="stl" weight="bold" size="small" >Table of content</Text>

                        </Box>
                        <Box  className="adj">
                        <Text className="stl" weight="bold" size="small" >Phrase Cloud</Text>

                        </Box>
                        
                    </Box>

            </Box>
            {searchResults.slice(firstindexofvideo,lastindexofvideo).map((value,index)=>{
                return(
                    <Videocomp tittle={value.tittle} addedon={value.addedon} checked={tick} ></Videocomp> 
                )
            })}
            <Box width="xxlarge" justify="center" direction="row" align="center">
                <Box width="100px" >
                    <Select  size="medium"
                    options={[5,10,20,30,40,50]}
                    value={contentperpage}
                    onChange={( {option} ) => {setcontentperpage(option); setvideoperpage(option);}}
                    />
                </Box>
            </Box>
            <Paging videoperpage={videoperpage} totalvideos={detail.length} paginate={paginate} ></Paging>

        </Box> 
            
        </>
    )
}
export default Vdeomngr;