
import React from 'react';
import {Box} from 'grommet';
import '../App.css';

function Paging({videoperpage,totalvideos,paginate}) {
    const pagenum =[];
    for(let i=1;i<=Math.ceil(totalvideos/videoperpage); i++){
        pagenum.push(i);
    }

    return (
        <>
            <Box width="xxlarge" direction="row" justify="center" align="center">
                <Box direction="row"  background="white" elevation="medium" >
                    {pagenum.map(num=>{
                        return(
                            <Box border focusIndicator="white" onClick={()=> paginate(num)} pad={{left:"small",right:"small"}} justify="center" align="center">{num}</Box>
                        )
                    })}
                </Box>
            </Box>

        </>
    )
}
export default Paging;